/* Burger menu */
.flex--end {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}

.header__inner {
  margin: 0;
}

.sidemenu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #3b3331;
  transform: translateX(-100%);
  transition: all 0.3s ease-in-out 0s;
  z-index: 2;
}

/* active class on menu */
.sidemenu.active {
  transform: translateX(0%);
}

#burger {
  cursor: pointer;
  width: 30px;
  height: 27px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

#burger > div {
  height: 4px;
  z-index: 999;
  border-radius: 20px;
  align-self: flex-end;
  width: 100%;
  background-color: #3b3331;
  transition: 0.2s ease-in-out;
}

.sidemenu ul li {
  list-style-type: none;
  margin-bottom: 10px;
}

.sidemenu ul li a {
  font-family: inherit;
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  text-transform: uppercase;
}

#burger div:nth-child(2) {
  width: 100%;
}

#burger div:nth-child(3) {
  width: 100%;
}

#burger:hover div {
  width: 100%;
}

/* active class on burger*/
#burger.active div:nth-child(1) {
  transform: translateY(11px) rotate(45deg);
  background-color: #fff;
  width: 100%;
}

#burger.active div:nth-child(2) {
  opacity: 0;
  background-color: #fff;
}

#burger.active div:nth-child(3) {
  transform: translateY(-12px) rotate(-45deg);
  background-color: #fff;
  width: 100%;
}

/*Main menu color*/
.main-navigation .main_menu ul.menu ul.sub-menu,
.main-navigation .main_menu ul.nav-menu ul.children {
  background-color: #d80019;
}

.primary-navigation a {
  color: #3b3331;
}

.primary-navigation a {
  font-size: 1rem;
}

.primary-navigation .sub-menu a {
  color: #fff !important;
}

.primary-navigation .sub-menu li:hover {
  background: #bf0016;
}

#site-navigation::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  left: 0;
  top: 12px;
  background: repeating-linear-gradient(90deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) 2px, transparent 0, transparent 6px);
}

#site-navigation::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  left: 0;
  bottom: 12px;
  background: repeating-linear-gradient(90deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) 2px, transparent 0, transparent 6px);
}

