.offcanvas-menu label span::before {
  transform: translateY(-12px);
}
.offcanvas-menu label span::after {
  transform: translateY(12px);
}
.offcanvas-menu .nav1 {
  position: fixed;
  height: 100%;
  width: 100vw;
  left: -100vw;
  overflow: hidden;
  background: #fff;
