  align-items: center;
  color: #3b3331;
}
.offcanvas-menu .nav1 > div a i {
  font-size: 16px;
  color: #3b3331;
}
.offcanvas-menu .nav1 > div label {
  width: 30px;
  height: 30px;
  display: flex !important;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: 0.3s ease;
}
.offcanvas-menu .nav1 > div label span,
.offcanvas-menu .nav1 > div label span:before,
.offcanvas-menu .nav1 > div label span:after {
  background: #3b3331;
