.offcanvas-menu input[type=checkbox] {
  display: none;
}
.offcanvas-menu input[type=checkbox]:checked ~ label {
  opacity: 0;
  pointer-events: none;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 {
  left: 0;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 label span {
  background: transparent;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 label span:before {
  transform: rotate(-45deg);
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 label span:after {
  transform: rotate(45deg);
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 label span:before,
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 label span:after {
  transition-delay: 0.2s;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 ul li {
  opacity: 1;
  transform: translateX(0px);
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 ul li:nth-child(1) {
  transition-delay: 0.1s;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 ul li:nth-child(2) {
  transition-delay: 0.2s;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 ul li:nth-child(3) {
  transition-delay: 0.3s;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 ul li:nth-child(4) {
  transition-delay: 0.4s;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 ul li:nth-child(5) {
  transition-delay: 0.5s;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 ul li:nth-child(6) {
  transition-delay: 0.6s;
}
.offcanvas-menu input[type=checkbox]:checked ~ .nav1 ul li:nth-child(7) {
  transition-delay: 0.7s;
}

