.offcanvas-menu {
	> label {
		top: 30px;
		left: 30px;
		width: 35px;
		height: 35px;
		cursor: pointer;
		transition: 0.3s ease;
		display: flex;
		align-items: center;
		z-index: 9999;
	}
	label {
		span,
		span::before,
		span::after {
			transition: background 0.3s, transform 0.3s;
		}
		span,
		span::before,
		span::after {
			content: "";
			position: absolute;
			height: 4px;
			width: 35px;
			background: $secondary;
		}
		span::before {
			transform: translateY(-12px);
		}
		span::after {
			transform: translateY(12px);
		}
	}
	.nav1 {
		position: fixed;
		height: 100%;
		width: 100vw;
		left: -100vw;
		overflow: hidden;
		background: $light;
		transition: 0.3s ease;
		padding: 40px 30px;
    top: 0;
    z-index: 2;

		> div {
			display: flex;
			justify-content: flex-end;
			a {
				font-size: 16px;
        text-transform: uppercase;
				font-weight: 500;
				display: flex;
				align-items: center;
				color: $secondary;
				i {
					font-size: 16px;
          color: $secondary;
				}
			}
			label {
				width: 30px;
				height: 30px;
				display: flex !important;
				align-items: center;
				justify-content: center;
				cursor: pointer;
				transition: 0.3s ease;
				span,
				span:before,
				span:after {
					background: $secondary;
				}
			}
		}
		> ul {
			margin-top: 30px;
			counter-reset: nav-link-count;

			li {
				cursor: pointer;
				padding: 10px 0;
				transform: translateX(-30px);
				opacity: 0;
				transition: 0.4s ease;
				counter-increment: nav-link-count;

				a {
					font-size: 16px;
					line-height: 1.5;
					font-weight: 400;
					color: $secondary;
					display: block;

					&::after {
						content: " : " counter(nav-link-count);
					}
				}

				&:hover {
					animation: hover 0.5s;
				}
			}
		}
	}

	input[type="checkbox"] {
		display: none;

		&:checked {
			~ label {
				opacity: 0;
				pointer-events: none;
			}
			~ .nav1 {
				left: 0;
			}

			~ .nav1 label span {
				background: transparent;
			}
			~ .nav1 label span:before {
				transform: rotate(-45deg);
			}
			~ .nav1 label span:after {
				transform: rotate(45deg);
			}
			~ .nav1 label span:before,
			~ .nav1 label span:after {
				transition-delay: 0.2s;
			}

			~ .nav1 ul li {
				opacity: 1;
				transform: translateX(0px);
			}
			~ .nav1 ul li:nth-child(1) {
				transition-delay: 0.1s;
			}
			~ .nav1 ul li:nth-child(2) {
				transition-delay: 0.2s;
			}
			~ .nav1 ul li:nth-child(3) {
				transition-delay: 0.3s;
			}
			~ .nav1 ul li:nth-child(4) {
				transition-delay: 0.4s;
			}
			~ .nav1 ul li:nth-child(5) {
				transition-delay: 0.5s;
			}
			~ .nav1 ul li:nth-child(6) {
				transition-delay: 0.6s;
			}
			~ .nav1 ul li:nth-child(7) {
				transition-delay: 0.7s;
			}
		}
	}
}
