/*Accordeon*/
.accordion-menu {
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  li {
    &.open > ul {
      display: block;
    }

    > ul {
      /* position: absolute; */
      display: none;
      height: 0;
      overflow: hidden;
      transition: height 350ms ease-in-out;
      margin-left: 25px;
    }
  }

  ul.is-visible {
    display: block;

    // height: auto; */
 
  }
  
  [aria-current='page'] {
    font-weight: 600;
  }

  li {
    border-bottom: 1px solid rgba(194, 194, 194, 0.333);
    position: relative;
    overflow: hidden;
    transition: all .4s ease;

    &:last-child {
      border: none;
    }

    &::after {
      content: "";
      display: block;
      clear: both;
    }

    > a > svg {
      display: block;
      position: relative;
      fill: $secondary;
      width: 10px;
      transition: all .3s ease;
      margin-left: 15px;
    }

    &.open > a > svg {
      transform: rotate(180deg);
    }
  }

  a {
    padding: 15px 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;
  }

  * {
    box-sizing: border-box;
  }
}